<template>
	<div>
		<p>usename:<input type="text" value="" placeholder="请输入用户名" v-model="user"/></p>
		<p>password: <input type="password" value="" placeholder="请输入密码" v-model="psw"/></p>
		<p v-if="!login">repassword: <input type="password" value="" placeholder="请再次输入" v-model="repsw"/></p>
		<p><button type="primary"  :style="{'background-color': login ? 'red' : 'transparent'}" @click="ll">登录</button>
		<button type="primary" @click="register" :style="{'background-color': login ? 'transparent' : 'red'}">注册</button></p>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				login:true,
				user:'',
				psw:'',
				repsw:''
			}
		},
		methods:{
			register(){
				const {user,psw,repsw} = this
				this.login = false
					setTimeout(function(){
						if(psw != repsw){
												alert('两次输入密码不一致')
												return
											}else if(!user){
													alert('请输入用户名')
													return
												}else if(!psw){
													alert('请输入密码')
													return
													}else{
													const usermsg = JSON.stringify({user,psw})
													sessionStorage.setItem('usermsg',usermsg)
													alert('注册成功')
												}
					})
				this.$nextTick(function(){
					
				})
				
				
				
			},
			ll(){
				this.login = true
				const {user,psw} = this
				if(!user){
					alert('请输入用户名')
					return
				}else if(!psw){
					alert('请输入密码')
					return
				}else{
					const usermsg = JSON.stringify({user,psw})
					sessionStorage.setItem('usermsg',usermsg)
					alert('登录成功')
				}
				this.$router.replace('/add')
			}
		},
		
	}
</script>

<style scoped="scoped">
	div{
		width: 1000px;
		margin: 20px auto;
		max-width: 600px;
		padding: 20px;
	}
</style>



