import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
	state:{
		blogs:[]
	},
	mutations:{
		
		ad(state,{data}){
			state.blogs.unshift(data)
			 console.log(state.blogs)
		}
	},
	actions:{
		addblog({commit},data){
			commit('ad',{data})
		}
	}
})


