<template>
	<div id="add-blog">
		<h3>添加博客</h3>
		<form action="" v-if="!sumitted">
		<lable for='title'>博客标题</lable> 
		<input type="text" value="" id="title" name='title' v-model="title" required="required"/>
		<lable for='content'>博客内容</lable>
		<textarea value="" placeholder="" id="content" v-model="content"/>
		<div id="check">
			<lable for='vue'>vue.js</lable>
			<input type="checkbox" value="vue.js" id="vue" v-model="checked"/>
			<lable for='node'>node.js</lable>
			<input type="checkbox" value="node.js" id="node" v-model="checked"/>
			<lable for='html5'>html5</lable>
			<input type="checkbox" value="html5" id="html5" v-model="checked"/>
			<lable for='css3'>css3</lable>
			<input type="checkbox" value="css3" id="css3" v-model="checked"/>
		</div>
		<!-- <lable for='author'>作者：</lable> -->
		<!-- <select name="" id="author" v-model="author">
			<option value="ni">ni</option>
			<option value="wo">wo</option>
			<option value="ta">ta</option>
		</select> -->
	    <button type="button" @click="addSingleBlog">添加博客</button>
		</form>
		<div v-if="sumitted">
			<h3>您的博客发布成功</h3>
		</div>
		<div id="preview">
			<h3>博客总览</h3>
			<p>博客标题：{{title}}</p>
			<p>博客内容：</p>
			<p>{{content}}</p>
			<p>博客分类：</p>
			<ul><li v-for="(check,index) in checked">{{check}}</li></ul>
			<p>作者：{{author}}</p>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				title:'',
				content:'',
				checked:[],
				author:'',
				sumitted:false
			}
		},
		methods:{
			addSingleBlog(){
				const usermsg = JSON.parse(sessionStorage.getItem('usermsg'))
				if(!usermsg){
					alert('请先登录')
					this.$router.replace('/login')
				} else{
					const data = {
						title:this.title,
						body:this.content
					}
					this.author = usermsg.user
					this.$store.dispatch('addblog',data)
					// alert('发布成功')
					// this.sumitted=true
// 					this.title=''
// 					this.content=''
				}
				
			}
		}
	}
</script>

<style scoped="scoped">
	#add-blog *{
		box-sizing: border-box;
	}
	#add-blog{
		margin: 20px auto;
		max-width: 600px;
		padding: 20px;
	}
	label{
		display: block;
		margin: 20px 0 10px;
	}
	input[type="text"],textarea,select{
		display: block;
		width: 100%;
		padding: 8px;
	}
	#check lable{
		display: inline-block;
		margin-top: 0;
	}
	#check input{
		display: inline-block;
		margin-right: 10px;
	}
	button{
		display: block;
		margin: 20px 0;
		background: crimson;
		color: white;
		border: 0;
		padding: 14px;
		border-radius: 4px;
		font-size: 10px;
		cursor: pointer;
	}
	#preview{
		padding: 10px 20px;
		border: 1px dotted #ccc;
		margin: 30px 0;
	}
	h3{
		margin-top: 10px;
	}
	textarea{
		height: 200px;
	}
</style>



