<template>
	<div id='single-blog' v-if="blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.body}}</article>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				id:this.$route.params.id,
				// blog:{}
			}
		},
		created(){
// 			this.$http.get('./../static/posts.json')
// 			.then(function(data){
// 				// console.log(data.body[1])
// 				this.blog = data.body[this.id]
// 			})
			
		},
		computed:{
			blog:function(){					
				return this.$store.state.blogs[this.$route.params.id]
			}		
		}		
	}
</script>

<style scoped="scoped">
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
	}
</style>



