<template>
	<div id='show-blogs'>
		<h1>博客总览</h1>
		<input type="text" placeholder="搜索" v-model="keyword">
			<div v-for="(comment,index) in dataShow" :key='index' class="single-blog">
				<router-link :to="`/blog/${index}`">
					<h2 v-rainbow>{{comment.title|upcase}}</h2>
				</router-link>
				<p>{{comment.body|haha}}</p>
			</div>
	</div>
</template>

<script>
	export default{
		name:'show-blogs',
		data(){
			return {
				keyword:'',
				// blogs:[]				
			}
		},
		created() {
			this.$http.get('./../static/posts.json')
			.then(function(data){
				 // console.log(data);
				this.$store.state.blogs = data.body.slice(0,2)
			})
	       
		},		
		computed:{
			dataShow:function(){
						  return  this.$store.state.blogs.filter((blog)=>
						  blog.title.match(this.keyword)
						  )
			}
		},
		filters:{
			upcase(data){
				return data.toUpperCase();
			},
			haha:function(value){
				return value.slice(0,100)+"...";}
		},
		directives:{
			'rainbow':{
				bind:function(el,binding,vnode){
					el.style.color='red';
				}
			}
		}
	}
</script>

<style scoped="scoped">
	#show-blogs{
		max-width: 800px;
		margin: 0 auto;
	}
	.single-blog{
		padding: 20px;
		margin: 20px 0;
		box-sizing: border-box;
		background: #eee;
		border: 1px dotted #aaa;
		}
	#show-blogs a{
	  color: #444;
	  text-decoration: none;
	}
	input[type="text"]{
	  padding: 8px;
	  width: 100%;
	  box-sizing: border-box;
	}
	
	
</style>



